import {comingSoon,newMovies,top250} from '../../api'
export default {
	data() {
		return {
			moviesList:[]
		}
	},
	onReady() {
		this.$store.dispatch("getCity");
		this.pageInit();
		
		},
	computed:{
		nowPlayingList(){
			// console.log(this.$store.state.nowPlayingList);
			return this.$store.state.nowPlayingList;
			},
			
	},
	methods: {
		pageInit(){
			uni.showLoading({
				title:'加载中'
			});
			Promise.all([
				this._comingSoon(),
				this._newMovies(),
				this._top250()
			]).then((res)=>{
				uni.hideLoading();
				let rel=[];
				res.map((item)=>{
					rel.push(item[1].data)
				})
				// console.log(rel);
				this.moviesList=rel;
				console.log(this.moviesList);
				})
			
		},
		toDetails(id){
			uni.navigateTo({
				url:"../details/details?id="+id
			})
		},
		toClass(title){
			uni.navigateTo({
				url:"../class/class?title="+title
			})
		},
		_comingSoon(){
			return comingSoon({
				start: "0",
				count: "8",
				city: this.$store.state.city
			})
		},
		_newMovies(){
			return newMovies({
				start: "0",
				count: "8",
				city: this.$store.state.city
			})
		},
		_top250(){
			return top250({
				start: "0",
				count: "8",
				city: this.$store.state.city
			})
		}
		
	}
}
